* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Navigation */
.nav-menu {
    display: flex;
    align-items: center;
    margin-left: 20px;
}
.brand-logo {
    width: 50%;
    height: 100%;
}
.nav-list {
    width: 50%;
    height: 50px;
    display: flex;
    margin-left: auto;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}

.nav-link {
    text-decoration: none;
    color: #000000;
}

/* All Pages  */
.page-wrapper {
    position: relative;
    height: calc(100vh - 50px);
    width: 100%;
}

.home, .about, .contract {
    height: 100%;
    width: 100%;
    position: absolute;
}

/* Home page Content */

.home {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.home-content, .about-content, .contract-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    img {
        width: 30%;
    }
    .home-presentation h2, .about-presentation, .contract-presentation h2 {
        font-size: 50px;
        margin: 30px 0px;
    }
    .home-presentation p, .about-presentation, .contract-presentation p {
        font-size: 20px;
    }
}

/* About page content */

.about {
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

/* Contract page content */
.contract {
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}